<template>
    <v-col
        cols="12" lg="6" sm="6" md="6" xs="6"
     >
        <v-card
        > 
            <v-row 
                justify="space-around" 
                :class="backgroundColor"
            >
                <v-card-title>Daily</v-card-title>
                <v-card-title>{{ dataGrab.cloud_position }}</v-card-title>
            </v-row>
            <v-card-text>
                <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">Price:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.price | toCurrency() }}</v-card-subtitle>
                </v-row>
                <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">Alert:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.entry }}</v-card-subtitle>
                </v-row>
                <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">RSI:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.rsi }}</v-card-subtitle>
                </v-row>
                <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">stochK:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.k }}</v-card-subtitle>
                
                    <v-card-subtitle class="text-center">stochD:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.d }}</v-card-subtitle>
                </v-row>
                <v-row justify="space-around" class="py-0">
                    <v-card-subtitle class="text-center">Candle Pattern:</v-card-subtitle>
                    <v-card-subtitle class="text-center">{{ dataGrab.pattern }}</v-card-subtitle>
                </v-row>                
            </v-card-text>     
        </v-card>
    </v-col>
</template>

<script>
export default {
    name: 'dailyStockLookup',
    computed: {
        dataGrab() {
            const stock = this.$route.params.id;
            const data = this.$store.state.all_daily[stock]
            return data
        },
        backgroundColor() {
            const data = this.$store.state.all_daily[this.$route.params.id]
            return data.cloud_position === 'Above' ? 'green white--text' : data.cloud_position === 'Below' ? 'red white--text' : data.cloud_position === 'In' ? 'yellow black--text' : 'white black--text'
        }
    },
    filters: {
        toCurrency(amount) {
            var formatter = new Intl.NumberFormat('en-US', {
                style: 'currency',
                currency: 'USD',
            });
            return formatter.format(amount)
        }
    },


}
</script>

